"use client";
import React from "react";
import { openSpecCard } from "@/components/spec/SpecCardPortal";

export function DemoOSTechFrame() {
  return (
    <section className="mx-auto max-w-5xl px-6">
      <div
        className="rounded-xl border bg-white/70 overflow-hidden"
        onMouseEnter={(e) =>
          openSpecCard({
            featureKey: "os-tech-frame",
            title: "OSTechFrame",
            tags: ["os", "frame", "window"],
            codeUrl: "https://github.com/your/repo/path/OSTechFrame.tsx",
            anchor: { x: e.clientX, y: e.clientY },
          })
        }
      >
        <div className="flex items-center gap-2 px-3 py-2 border-b bg-black/5 text-xs">
          <span className="h-3 w-3 rounded-full bg-red-400 inline-block" />
          <span className="h-3 w-3 rounded-full bg-yellow-400 inline-block" />
          <span className="h-3 w-3 rounded-full bg-green-400 inline-block" />
          <span className="ml-2 font-medium">window.tsx</span>
        </div>
        <div className="p-4 text-sm text-black/70">
          An OS-style window with traffic-light controls. Mouse over to surface its spec.
        </div>
      </div>
    </section>
  );
}
