"use client";
import React from "react";
import { openSpecCard } from "@/components/spec/SpecCardPortal";

export function DemoMinimalistLanding() {
  return (
    <section
      className="mx-auto max-w-4xl text-center px-6 py-14 rounded-2xl border bg-white/60"
      onClick={(e) =>
        openSpecCard({
          featureKey: "minimalist-landing",
          title: "MinimalistLanding",
          tags: ["landing", "minimal", "cta"],
          codeUrl: "https://github.com/your/repo/path/MinimalistLanding.tsx",
          anchor: { x: e.clientX, y: e.clientY },
        })
      }
    >
      <h2 className="text-3xl md:text-5xl font-semibold tracking-tight">Fast. Quiet. Precise.</h2>
      <p className="mt-3 text-black/70">A clean landing header with restraint and a single intent.</p>
      <div className="mt-6 flex justify-center gap-3">
        <button className="px-4 py-2 rounded-md bg-black text-white text-sm">Get Started</button>
        <button className="px-4 py-2 rounded-md border text-sm">See Work</button>
      </div>
    </section>
  );
}
