"use client";
import React from "react";
import { openSpecCard } from "@/components/spec/SpecCardPortal";

export function DemoPhotographyGallery() {
  const imgs = ["/next.svg", "/vercel.svg", "/window.svg", "/globe.svg", "/file.svg", "/next.svg"];
  return (
    <section className="mx-auto max-w-5xl px-6">
      <h2 className="text-xl font-medium mb-3">Photography Grid</h2>
      <div className="grid grid-cols-2 md:grid-cols-3 gap-3">
        {imgs.map((src, i) => (
          <figure
            key={i}
            className="aspect-[4/3] rounded-xl border bg-white/70 grid place-items-center cursor-pointer"
            onClick={(e) =>
              openSpecCard({
                featureKey: "photo-grid",
                title: "PhotographyGallery",
                tags: ["gallery", "grid", "photos"],
                codeUrl: "https://github.com/your/repo/path/PhotographyGallery.tsx",
                anchor: { x: e.clientX, y: e.clientY },
              })
            }
          >
            <img src={src} alt="" className="h-12 opacity-70" />
          </figure>
        ))}
      </div>
    </section>
  );
}
