"use client";
import React from "react";
import { openSpecCard } from "@/components/spec/SpecCardPortal";

export function DemoMarqueeBanner() {
  return (
    <section className="mx-auto max-w-5xl px-6">
      <h2 className="text-xl font-medium mb-3">Marquee Banner</h2>
      <div
        className="overflow-hidden rounded-xl border bg-white/70 cursor-pointer"
        onClick={(e) =>
          openSpecCard({
            featureKey: "marquee-banner",
            title: "MarqueeBanner",
            tags: ["marquee", "banner", "announcements"],
            codeUrl: "https://github.com/your/repo/path/MarqueeBanner.tsx",
            anchor: { x: e.clientX, y: e.clientY },
          })
        }
      >
        <div className="whitespace-nowrap animate-[marquee_12s_linear_infinite] text-sm p-2">
          <span className="mx-6">New build slots opening next week</span>
          <span className="mx-6">Ownership-first web apps</span>
          <span className="mx-6">Zero template lock-in</span>
        </div>
      </div>
      <style>{`@keyframes marquee{0%{transform:translateX(0)}100%{transform:translateX(-50%)}}`}</style>
    </section>
  );
}
