"use client";
import React, { useEffect, useState } from "react";
import { openSpecCard } from "@/components/spec/SpecCardPortal";

export function DemoStickyCTA() {
  const [show, setShow] = useState(false);
  useEffect(() => {
    const onScroll = () => setShow(window.scrollY > 300);
    window.addEventListener("scroll", onScroll, { passive: true });
    return () => window.removeEventListener("scroll", onScroll);
  }, []);
  return (
    <>
      <section className="mx-auto max-w-3xl px-6">
        <h2 className="text-xl font-medium mb-3">Sticky CTA</h2>
        <p className="text-sm text-black/70">Scroll down to reveal the sticky call-to-action.</p>
        <div className="h-48" />
      </section>
      {show && (
        <div
          className="fixed bottom-4 left-1/2 -translate-x-1/2 rounded-full border bg-white/90 backdrop-blur px-5 py-2 shadow-md cursor-pointer"
          onClick={(e) =>
            openSpecCard({
              featureKey: "sticky-cta",
              title: "StickyCTA",
              tags: ["cta", "sticky", "scroll"],
              codeUrl: "https://github.com/your/repo/path/StickyCTA.tsx",
              anchor: { x: e.clientX, y: e.clientY },
            })
          }
        >
          Letâ€™s build it â†’
        </div>
      )}
    </>
  );
}
