"use client";
import React, { useState } from "react";
import { openSpecCard } from "@/components/spec/SpecCardPortal";

const QA = [
  { q: "Whatâ€™s included?", a: "Design, build, performance tuning, and handover." },
  { q: "Do I own it?", a: "Yes. 100% ownership, no templates, no lock-in." },
  { q: "How fast?", a: "Depends on scope, but fast by default." },
];

export function DemoAccordionFAQ() {
  const [open, setOpen] = useState<number | null>(0);
  return (
    <section className="mx-auto max-w-3xl px-6">
      <h2 className="text-xl font-medium mb-3">Accordion FAQ</h2>
      <ul className="rounded-xl border bg-white/60 divide-y">
        {QA.map((row, i) => (
          <li key={i}>
            <button
              className="w-full flex items-center justify-between px-4 py-3 text-left"
              onClick={(e) => {
                setOpen((v) => (v === i ? null : i));
                openSpecCard({
                  featureKey: "accordion-faq",
                  title: "AccordionFAQ",
                  tags: ["accordion", "faq", "disclosure"],
                  codeUrl: "https://github.com/your/repo/path/AccordionFAQ.tsx",
                  anchor: { x: (e as any).clientX ?? 200, y: (e as any).clientY ?? 120 },
                });
              }}
            >
              <span className="font-medium">{row.q}</span>
              <span>{open === i ? "â€“" : "+"}</span>
            </button>
            {open === i && <div className="px-4 pb-3 text-sm text-black/70">{row.a}</div>}
          </li>
        ))}
      </ul>
    </section>
  );
}
