"use client";
import React, { useState } from "react";
import { openSpecCard } from "@/components/spec/SpecCardPortal";

export function DemoExpandCollapseSidebar() {
  const [open, setOpen] = useState(true);
  return (
    <section className="mx-auto max-w-5xl px-6">
      <h2 className="text-xl font-medium mb-3">Expand/Collapse Sidebar</h2>
      <div className="flex border rounded-xl overflow-hidden">
        <aside
          className={`transition-[width] duration-300 bg-white/70 backdrop-blur border-r ${open ? "w-64" : "w-12"}`}
          onClick={(e) =>
            openSpecCard({
              featureKey: "expand-sidebar",
              title: "ExpandCollapseSidebar",
              tags: ["sidebar", "layout", "resize"],
              codeUrl: "https://github.com/your/repo/path/ExpandCollapseSidebar.tsx",
              anchor: { x: e.clientX, y: e.clientY },
            })
          }
        >
          <button
            onClick={() => setOpen((v) => !v)}
            className="w-full text-left px-3 py-2 text-sm hover:bg-black/5"
          >
            {open ? "âŸ¨âŸ¨ Collapse" : "âŸ©âŸ©"}
          </button>
          {open && (
            <ul className="px-3 pb-3 text-sm space-y-1">
              <li className="hover:underline cursor-pointer">Dashboard</li>
              <li className="hover:underline cursor-pointer">Orders</li>
              <li className="hover:underline cursor-pointer">Analytics</li>
            </ul>
          )}
        </aside>
        <main className="flex-1 p-4">
          <p className="text-sm text-black/70">Click the sidebar to open its Spec Card.</p>
          <div className="h-40 rounded-lg border bg-white/60 mt-3" />
        </main>
      </div>
    </section>
  );
}
