"use client";
import React from "react";
import { openSpecCard } from "@/components/spec/SpecCardPortal";

export function DemoSplitScreen() {
  return (
    <section className="mx-auto max-w-5xl px-6">
      <div
        className="grid grid-cols-1 md:grid-cols-2 rounded-xl border overflow-hidden"
        onMouseEnter={(e) =>
          openSpecCard({
            featureKey: "split-screen",
            title: "SplitScreen",
            tags: ["layout", "split", "hero"],
            codeUrl: "https://github.com/your/repo/path/SplitScreen.tsx",
            anchor: { x: e.clientX, y: e.clientY },
          })
        }
      >
        <div className="p-6 bg-white/70">
          <h3 className="text-2xl font-semibold">Left: Message</h3>
          <p className="text-sm text-black/70 mt-2">Copy/content lives here with a clean CTA.</p>
        </div>
        <div className="bg-[url('/window.svg')] bg-center bg-no-repeat bg-white/40 min-h-[180px]" />
      </div>
    </section>
  );
}
