"use client";
import React, { useState } from "react";
import { openSpecCard } from "@/components/spec/SpecCardPortal";

export function DemoModal() {
  const [open, setOpen] = useState(false);
  return (
    <section className="mx-auto max-w-5xl px-6">
      <h2 className="text-xl font-medium mb-3">Modal</h2>
      <button
        className="px-3 py-2 rounded-md border text-sm"
        onClick={(e) => {
          setOpen(true);
          openSpecCard({
            featureKey: "modal",
            title: "Modal",
            tags: ["dialog", "overlay"],
            codeUrl: "https://github.com/your/repo/path/Modal.tsx",
            anchor: { x: (e as any).clientX ?? 200, y: (e as any).clientY ?? 120 },
          });
        }}
      >
        Open Modal
      </button>
      {open && (
        <div className="fixed inset-0 bg-black/40 grid place-items-center">
          <div className="rounded-xl border bg-white p-4 w-[360px]">
            <div className="font-medium mb-2">Modal Title</div>
            <p className="text-sm text-black/70">A simple modal with backdrop.</p>
            <div className="mt-3 text-right">
              <button className="px-3 py-2 rounded-md border text-sm" onClick={() => setOpen(false)}>
                Close
              </button>
            </div>
          </div>
        </div>
      )}
    </section>
  );
}
