"use client";
import React, { useState } from "react";
import { openSpecCard } from "@/components/spec/SpecCardPortal";

export function DemoTooltip() {
  const [show, setShow] = useState(false);
  return (
    <section className="mx-auto max-w-5xl px-6">
      <h2 className="text-xl font-medium mb-3">Tooltip</h2>
      <div
        className="inline-block relative"
        onMouseEnter={(e) => {
          setShow(true);
          openSpecCard({
            featureKey: "tooltip",
            title: "Tooltip",
            tags: ["hover", "help"],
            codeUrl: "https://github.com/your/repo/path/Tooltip.tsx",
            anchor: { x: e.clientX, y: e.clientY },
          });
        }}
        onMouseLeave={() => setShow(false)}
      >
        <button className="px-3 py-2 rounded-md border text-sm">Hover me</button>
        {show && <div className="absolute left-1/2 -translate-x-1/2 mt-2 text-xs bg-black text-white px-2 py-1 rounded">Helpful tip</div>}
      </div>
    </section>
  );
}
