"use client";
import React, { useState } from "react";
import { openSpecCard } from "@/components/spec/SpecCardPortal";

export function DemoTabs() {
  const [tab, setTab] = useState<"a" | "b" | "c">("a");
  const T = (id: "a" | "b" | "c", label: string) => (
    <button
      key={id}
      onClick={(e) => {
        setTab(id);
        openSpecCard({
          featureKey: "tabs",
          title: "Tabs",
          tags: ["tabs", "navigation"],
          codeUrl: "https://github.com/your/repo/path/Tabs.tsx",
          anchor: { x: (e as any).clientX ?? 200, y: (e as any).clientY ?? 120 },
        });
      }}
      className={`px-3 py-1 rounded-md border text-sm ${tab === id ? "bg-black text-white" : ""}`}
    >
      {label}
    </button>
  );
  return (
    <section className="mx-auto max-w-5xl px-6">
      <h2 className="text-xl font-medium mb-3">Tabs</h2>
      <div className="flex gap-2 mb-3">{["a","b","c"].map((k,i)=>T(k as any, `Tab ${i+1}`))}</div>
      <div className="rounded-xl border bg-white/70 p-4 text-sm">Current: <strong>{tab.toUpperCase()}</strong></div>
    </section>
  );
}
