"use client";
import React, { useState } from "react";
import { openSpecCard } from "@/components/spec/SpecCardPortal";

export function DemoToast() {
  const [show, setShow] = useState(false);
  return (
    <section className="mx-auto max-w-5xl px-6">
      <h2 className="text-xl font-medium mb-3">Toast</h2>
      <button
        className="px-3 py-2 rounded-md border text-sm"
        onClick={(e) => {
          setShow(true);
          setTimeout(() => setShow(false), 1500);
          openSpecCard({
            featureKey: "toast",
            title: "Toast",
            tags: ["feedback", "toast", "notification"],
            codeUrl: "https://github.com/your/repo/path/Toast.tsx",
            anchor: { x: (e as any).clientX ?? 200, y: (e as any).clientY ?? 120 },
          });
        }}
      >
        Trigger Toast
      </button>
      {show && (
        <div className="fixed bottom-6 right-6 rounded-md bg-black text-white text-sm px-3 py-2 shadow">
          Saved!
        </div>
      )}
    </section>
  );
}
