"use client";
import React, { useEffect, useState } from "react";
import { openSpecCard } from "@/components/spec/SpecCardPortal";

export function DemoParallaxHero() {
  const [y, setY] = useState(0);
  useEffect(() => {
    const onScroll = () => setY(window.scrollY);
    window.addEventListener("scroll", onScroll, { passive: true });
    return () => window.removeEventListener("scroll", onScroll);
  }, []);
  return (
    <section
      className="relative h-[50vh] overflow-hidden rounded-xl border mx-auto max-w-5xl px-0"
      onMouseEnter={(e) =>
        openSpecCard({
          featureKey: "parallax-hero",
          title: "ParallaxHero",
          tags: ["parallax", "motion", "hero"],
          codeUrl: "https://github.com/your/repo/path/ParallaxHero.tsx",
          anchor: { x: e.clientX, y: e.clientY },
        })
      }
    >
      <div
        className="absolute inset-0 bg-[url('/vercel.svg')] bg-no-repeat bg-center bg-contain opacity-10"
        style={{ transform: `translateY(${y * 0.1}px)` }}
      />
      <div
        className="absolute inset-0 bg-[url('/globe.svg')] bg-no-repeat bg-center opacity-25"
        style={{ transform: `translateY(${y * 0.2}px)` }}
      />
      <div className="relative z-10 h-full grid place-items-center">
        <div className="text-center">
          <h2 className="text-3xl md:text-5xl font-semibold">Parallax Hero</h2>
          <p className="text-black/70 mt-2">Scroll to feel depth. Hover for Spec Card.</p>
        </div>
      </div>
    </section>
  );
}
