"use client";
import React from "react";
import { openSpecCard } from "@/components/spec/SpecCardPortal";

export function DemoPricingMatrix() {
  const tiers = [
    { name: "Starter", price: "$", points: ["1 page", "Email capture", "Basic styling"] },
    { name: "Pro", price: "$$", points: ["Up to 5 pages", "CMS", "Animations"] },
    { name: "Custom", price: "$$$", points: ["Unlimited", "Integrations", "Ownership"] },
  ];
  return (
    <section className="mx-auto max-w-5xl px-6">
      <h2 className="text-xl font-medium mb-3">Pricing Matrix (Illustrative)</h2>
      <div className="grid grid-cols-1 md:grid-cols-3 gap-3">
        {tiers.map((t) => (
          <div
            key={t.name}
            className="rounded-xl border bg-white/70 backdrop-blur p-4 cursor-pointer"
            onClick={(e) =>
              openSpecCard({
                featureKey: "pricing-matrix",
                title: "PricingMatrix",
                tags: ["pricing", "cards", "grid"],
                codeUrl: "https://github.com/your/repo/path/PricingMatrix.tsx",
                anchor: { x: e.clientX, y: e.clientY },
              })
            }
          >
            <div className="text-sm uppercase tracking-wide">{t.name}</div>
            <div className="text-2xl font-semibold my-2">{t.price}</div>
            <ul className="text-sm text-black/70 space-y-1">
              {t.points.map((p) => <li key={p}>â€¢ {p}</li>)}
            </ul>
          </div>
        ))}
      </div>
    </section>
  );
}
